*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.counterContainer{
    display: flex;

}
.App{
    display: flex;
    flex-direction: column;
}
.App>div{
    /*margin-top: 20px;*/
}
.menu .isdone{
    background-color: #03fded;

}
.settings input{
    max-width: 60px;
    background-color: antiquewhite;
}
.settings{
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
    height: 90%;
    margin-left: 25px;
    display: flex;
    background-color: grey;


}
.App{
    position: relative;

    margin: 0 auto;
    background-color: antiquewhite;
}
.menu{
    margin-bottom: 30px;
}
.App-container{
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #9f9b9b;

    margin: 10px auto;
}
.counter {

    background-color: rgba(134, 158, 217, 0.55);
    border-radius: 8px;
    margin: 0 5px 20px 5px;
}

.display{
    color: blue;
    font-size: 25px;
    margin-top: 10px;
    width: 120px;

    background-color:antiquewhite;
}
.display > span{
    font-weight: bold;
    font-size: 14px;
    color: #8f7a07;

}
.display > span+span{
    margin-left: 10px;
}
.remuve-counter{
    font-weight: bold;
    color: crimson;
    padding: 5px;
    border-radius: 50%;
    background-color: rgba(2, 255, 62, 0.87);
    margin-right: 10%;
}
.countInterface{
    background-color: rgba(64, 68, 236, 0.77);
    border-radius: 5px;
    border: 2px solid grey;
}
.menu button{
    background-color: #05e76c;
    padding: 2px;
    border-radius: 5px;
}
.menu button:last-child{
    background-color: #e9ef99;


}
.settings-step button{
    background-color: #e9ef99;
    border-radius: 4px;
    padding: 0 5px 0 5px;

}
.settings-step input{
    background-color: #aade6c;
}
.settings-limit input{
    background-color: #aade6c;
}
.settings-step span{
    letter-spacing: 1.2px;
    font-weight: bold;
    color:antiquewhite;
    margin: 0 8px 2px 15px;
}
.settings-limit span{
    letter-spacing: 1.2px;
    font-weight: bold;
    color:antiquewhite;
    margin: 0 8px 2px 15px;
}
.settings-eror{
    font-weight: bold;
    letter-spacing: 1.6px;
    font-size: 25px;
    color: crimson;
    margin: 15px 0 0 55px;
    padding: 10px;
    background-color: rgba(17, 250, 175, 0.47);
}
.settings-step{
    margin-top: 35px;
}
.settings-limit{
    margin-top: 35px;
}
.controlContainer>button+button{
    margin-left: 35px;
}
.controlContainer>button{
    background-color: #e9ef99;
    border-radius: 5px;
    margin-top: 10px;
    margin-bottom: 8px;
}

